<template>
    <div class="full-img-bg px-3 pt-3 px-md-5 pt-md-5" v-bind:style="{ 'background-image': `url(${bgSrc}` }">

      <div class="rounded login-card text-white text-center mx-md-5 px-5 p-4">
        <div class="d-none d-sm-block">
          <h1>Welcome to InstaB.io!</h1>
          <h5 class="my-3">ðŸ¤© A WYSIWYG Social Media Portfolio ðŸ”¥</h5>
        </div>

        <div class="d-block d-sm-none">
          <h1>InstaB.io ðŸ”¥</h1>
          <h5 class="my-3">ðŸ¤© WYSIWYG Social Portfolio</h5>
        </div>

        <hr />

        <button type="button" class="btn btn-facebook" @click="loginFacebook">
          <font-awesome-icon :icon="['fab', 'facebook-f']" />acebook
        </button>

        <div><p class="my-4">-- OR --</p></div>

        <button type="button" class="btn btn-google" @click="loginGoogle">
          <font-awesome-icon :icon="['fab', 'google']" />mail
        </button>

        <div><p class="my-4">-- OR --</p></div>

        <button type="button" class="btn btn-twitter" @click="loginTwitter">
          <font-awesome-icon :icon="['fab', 'twitter']" />Twitter
        </button>

        <hr />

        <a class="text-reset" target="_blank" href="https://blog.instab.io/#/guide/instab.io-101">Need some help<font-awesome-icon class="mx-1 fa-lg mr-3" :icon="['fa', 'question']"/>ðŸ¤“ Read the Guide of Use ðŸ“š</a>
      </div>

      <div class="text-right mt-1 mt-lg-2 mr-2 mr-lg-5">
        <img src="@/assets/beta.png" style="width: 200px;"/>
      </div>

    </div>
</template>

<script language="ts">
import firebase from 'firebase/app'
import { auth } from '../firebase.config'
import { getRandomFromArr, getEmojis, randomInRange } from '../utils/utils'
import bgImgs from '@/assets/bg_imgs.json'
import profileImgs from '@/assets/profile_imgs.json'

export default {
  metaInfo: {
    title: 'Login'
  },
  computed: {
    bgSrc () { return getRandomFromArr([...bgImgs, ...profileImgs]) }
  },
  methods: {
    threeEmojis () { return getEmojis(randomInRange(1, 3)) },
    async loginWithPopUp (authProvider) {
      try {
        await auth.signInWithPopup(authProvider)
        this.$router.push(`/${auth.currentUser.uid}/edit`)
      } catch ({ code, message }) {
        console.error({ code, message })
        this.$bvToast.toast(message, {
          title: 'Error',
          variant: 'warning',
          toaster: 'b-toaster-top-right'
        })
      }
    },
    loginFacebook () {
      this.loginWithPopUp(new firebase.auth.FacebookAuthProvider())
    },
    loginGoogle () {
      this.loginWithPopUp(new firebase.auth.GoogleAuthProvider())
    },
    loginTwitter () {
      this.loginWithPopUp(new firebase.auth.TwitterAuthProvider())
    }
  }
}
</script>

<style lang="css" scoped>
/* Custom CSS here */
.full-img-bg {
  height: 100%;
  width: 100%;
  background-position: center center;
  background-repeat:  no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.login-card {
  background-color: rgba(0, 0, 0, 0.2)
}
</style>
